html {
  box-sizing: border-box;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;


  font-family: "FivoSans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 40px;
  color: #111111;
  font-style: normal;

  background-image: url("/img/background.png");
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding-left: 30px;
  padding-right: 30px;
}
