.page-header {
  padding: 20px 0;
}
.page-header__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.page-header__logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.main-nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 52%;
}
.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  margin-right: 125px;
  width: 64%;
}
.site-list__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 15%;
}
.site-list__item:last-child {
  margin-right: 0;
}
.site-list__link {
  display: block;
  line-height: 36px;
  letter-spacing: normal;
  text-align: center;
  color: rgba(28, 32, 86, 0.5);
  text-decoration: none;
}
.site-list__link--active {
  color: #ff6a6d;
}
.user-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.page-main__container {
  padding: 0 30px;
}
.page-main__intro {
  margin-top: 70px;
  margin-bottom: 200px;
}
.page-main__features {
  margin-bottom: 200px;
}
.page-main__prices {
  margin-bottom: 200px;
}
.page-main__info {
  margin-bottom: 200px;
}
.intro {
  padding: 0 54% 0 8%;
}
.intro__title {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  font-size: 50px;
  line-height: 61px;
  color: #111111;
}
.intro__description {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  line-height: 40px;
  color: rgba(0, 0, 0, 0.65);
}
.intro__menu {
  display: flex;
  flex-direction: row;
}
.intro__button:first-child {
  margin-right: 20%;
}
.features__title {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  font-weight: 800;
  font-size: 50px;
  line-height: 61px;
  text-align: center;
  color: #111111;
}
.features__description {
  margin: 0;
  margin-bottom: 100px;
  padding: 0 350px;
  font-weight: lighter;
  text-align: center;
  color: rgba(0, 0, 0, 0.65);
}
.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.features__item {
  flex-basis: 271px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.features__item-title {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  font-weight: 800;
  font-size: 24px;
  line-height: 61px;
  text-align: center;
  color: #111111;
}
.features__item-description {
  margin: 0;
  padding: 0;
  line-height: 27px;
  text-align: center;
  color: rgba(0, 0, 0, 0.65);
}
.info__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.info__list-item {
  margin-bottom: 150px;
}
.info__list-item:last-child {
  margin-bottom: 0;
}
.info-item {
  padding-left: 50%;
  position: relative;
}
.info-item::after {
  content: "";
  position: absolute;
  top: 0px;
  left: -22px;
  width: 720px;
  height: 696px;
  background-image: url("/img/pose-background.png");
  background-repeat: no-repeat;
  z-index: -100;
}
.info-item:nth-child(2)::after {
  background-image: url("/img/pose2-background.png");
  left: inherit;
  right: -60px;
  top: -150px;
}
.info-item:nth-child(3)::after {
  background-image: url("/img/pose3-background.png");
  height: 750px;
  top: -65px;
}
.info-item__title {
  margin: 0;
  margin-bottom: 65px;
  padding: 0;
  font-size: 50px;
  line-height: 61px;
  font-weight: 800;
}
.info-item__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-left: 85px;
}
.info-item__item {
  margin-bottom: 40px;
}
.info-item__description {
  margin: 0;
  color: rgba(0, 0, 0, 0.65);
}
.info-item__item-title {
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 40px;
  font-weight: 800;
}
.info-item__min-title {
  margin: 0;
  padding: 0;
  font-size: 20px;
  line-height: 40px;
  font-style: oblique;
  color: rgba(0, 0, 0, 0.51);
}
.info-item__item-description {
  margin: 0;
  padding: 0;
  padding-right: 150px;
  color: rgba(0, 0, 0, 0.65);
}
.info-item--left-text {
  padding: 0;
  padding-right: 50%;
}
.info-item__link {
  font-size: 18px;
  line-height: 40px;
  letter-spacing: normal;
  color: #3cbef1;
  text-decoration: none;
  font-style: italic;
}
.prices {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-image: url("/img/Ellipse.png");
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
}
.price-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-basis: 250px;
  padding: 30px 50px;
  height: 470px;
  border: 3px solid #68dffd;
  border-radius: 25px;
  background-color: white;
}
.price-card__header {
  display: block;
  margin-bottom: 50px;
  align-self: center;
  font-size: 30px;
  color: rgba(73, 73, 73, 0.8);
  font-weight: 800;
}
.price-card__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.price-card__item {
  margin-bottom: 20px;
  color: rgba(59, 56, 56, 0.75);
}
.price-card__item:last-child {
  margin-bottom: 40px;
}
.price-card--promo {
  height: 570px;
  flex-basis: 325px;
}
.price-card--promo .price-card__header {
  color: #ff6a6d;
}
.price-card__price-wrapper {
  align-self: center;
}
.price-card__price {
  color: rgba(59, 56, 56, 0.75);
  font-size: 150px;
}
.price-card__price-period {
  color: rgba(59, 56, 56, 0.75);
  font-size: 30px;
}
.price-card__periodicity {
  align-self: center;
  font-style: italic;
  color: rgba(59, 56, 56, 0.75);
}
.button {
  display: block;
  padding: 10px 30px;
  line-height: 36px;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  background-color: #ff6a6d;
  border-radius: 40px;
}
.button--transparent {
  color: #ff6a6d;
  background-color: transparent;
  border: 2px #ff6a6d solid;
}
.button--long {
  padding: 10px 50px;
}
.button--blue {
  background: linear-gradient(to bottom left, #21acdd, #9ddae9);
}
.page-footer {
  padding: 0 80px;
}
.page-footer__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 40px;
}
.page-footer__item {
  flex-basis: 250px;
}
.page-footer__title {
  margin: 0;
  padding: 0;
  margin-bottom: 15px;
  font-weight: 800;
  text-transform: uppercase;
  color: rgba(46, 46, 46, 0.7);
}
.page-footer__address {
  margin: 0;
  margin-bottom: 15px;
  font-size: 16px;
  line-height: 30px;
}
.page-footer__link {
  font-size: 16px;
  line-height: 30px;
  color: rgba(46, 46, 46, 0.7);
  text-decoration: none;
}
.page-footer__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.page-footer__copyright {
  display: block;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  font-size: 18px;
  line-height: 36px;
  letter-spacing: 0.1em;
  color: rgba(46, 46, 46, 0.7);
}
